@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

root {
    --Poppins: 'Poppins', sans-serif;
    --font-Weight-100: 100;
    --font-Weight-200: 200;
    --font-Weight-300: 300;
    --font-Weight-400: 400;
    --font-Weight-500: 500;
    --font-Weight-600: 600;
    --font-Weight-700: 700;
}

*,
*::after,
*::before {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
    width: 100%;
    font-family: var(--Poppins);
}

body {
    overflow-x: hidden;
}